
import "github-syntax-light/lib/github-light.css";
import "../style/gitment.scss";
import "../style/toc.scss";
import { github } from "../helpers/github";
import Render from "../helpers/render";
import Gitment from "../directives/gitment";
import { date_format } from "@/helpers/utils";
import tocbot from "tocbot";

export default {
  name: "Detail",
  methods: {},
  computed: {
    userLink(): string {
      return "https://github.com/" + this.detail.user.login;
    },
    createAt() {
      return date_format(this.detail.created_at);
    },
    avatar() {
      return this.detail.user.avatar_url + "&s=20";
    }
  },
  data() {
    return {
      loading: true,
      detail: {},
      status: "正在加载...",
      has_toc: false
    };
  },
  mounted() {
    Render.loading_start();
  },
  unmounted() {
    this.loading = true;
  },
  watch: {
    detail: {
      handler(val, oldVal) {
        if (!this.had_toc && !this.loading) {
          Render.general_ids();
          tocbot.init({
            // Where to render the table of contents.
            tocSelector: "#markdown-toc",
            // Where to grab the headings to build the table of contents.
            contentSelector: ".markdown-body",
            // Which headings to grab inside of the contentSelector element.
            headingSelector: "h1, h2, h3"
          });
          this.had_toc = true;
          let flag = this.$route.params.id;
          if (this.loading) document.title = "loading ---- 青枫浦 Lite";
          const gitment = new Gitment({
            id: flag, // optional
            meta: this.detail
          });
          gitment.render("comments");
        }
      }
    }
  },
  created() {
    let flag = this.$route.params.id;
    github.getDetail(flag).then(
        res => {
          this.status = "正在解析...";
          this.loading = false;
          this.detail = res;
          //this.detail.html = ''//rend(flag,this.detail.body);
          document.title = res.title;
          Render.loading_end();
        },
        res => {
          this.status = "从服务端数据失败...";
        }
    );
  }
};
